import OpenChecklist from "./OpenChecklist";
import MakeCheckListInactive from "./MakeChecklistInactive";

const ChooseRow = ({item,idx}) => {
    return <tr>
        <td>{idx+1}</td>
        <td>{item.name}</td>
        <td>{item.description}</td>
        <td><OpenChecklist item={item} /></td>
        <td><MakeCheckListInactive id={item.id}/> </td>
    </tr>
}

export default ChooseRow;